<style scoped lang="scss">
.row {
  color: #fff;
  margin-left: 20px;
  .title {
    font-size: 0.9rem;
  }
  .row_posters {
    display: flex;
    overflow-y: hidden;
    overflow-x: scroll;
    padding: 20px;
    &::-webkit-scrollbar {
      display: none;
    }
    .row_poster {
      max-height: 140px;
      object-fit: contain;
      margin-right: 10px;
      width: 100%;
      transition: transform 450ms;
      &:hover {
        transform: scale(1.09);
        opacity: 1;
      }
    }
    .row_posterLarge {
      max-height: 250px;
      &:hover {
        transform: scale(1.09);
        opacity: 1;
      }
    }
  }
}
</style>
